import { StatusBar } from 'expo-status-bar';
import React, {useState, useEffect} from 'react';
import { StyleSheet, Text, View, Button, TouchableOpacity, Dimensions, useWindowDimensions } from 'react-native';

export default function App() {
  interface DimensionState {
    width: number,
    height: number
  };
  const initState: DimensionState = {
    width: Dimensions.get('window').width,
    height: Dimensions.get('window').height
  }
  const DimensionContext = React.createContext<DimensionState>(initState);
  const DimensionsProvider = (prop: {children: React.ReactNode}) => {
    const dimensions: DimensionState = useWindowDimensions();
    return <DimensionContext.Provider value={dimensions}>{prop.children}</DimensionContext.Provider>
  }

  const TestContents = () => {
    const {width, height} = React.useContext(DimensionContext);
    return (
      <View style={styles.container}>
        <View style={styles.header}>
          <Text>TITLE</Text>
        </View>
        <View style={[styles.contents, {height: height - 100}]}>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
          <Text style={styles.standardText}>SIZE: {width} x {height}</Text>
        </View>
        <View style={styles.footer}>
          <Text>END</Text>
        </View>
      </View>
    )
  }

  return (
    <DimensionsProvider><TestContents /></DimensionsProvider>
  );
}

const styles = StyleSheet.create({
  container: {
    width: '100%',
    height: '100%',
    alignItems: 'center'
  },
  header: {
    width: '100%',
    height: 50,
    textAlign: 'center',
    borderBottomWidth: 1,
    borderBottomColor: 'rgba(0,0,0,0.2)'
  },
  contents: {
    width: '100%',
    padding: 25,
    overflow: 'scroll'
  },
  standardText: {
    fontSize: 28,
    lineHeight: 40,
    marginVertical: 7
  },
  footer: {
    position: 'absolute',
    bottom: 0,
    width: '100%',
    height: 50,
    textAlign: 'center',
    borderTopWidth: 1,
    borderTopColor: 'rgba(0,0,0,0.2)'
  }
});
